import React, { Component } from 'react'
import { Menu, Dropdown } from 'semantic-ui-react'
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import science from '../Images/science.svg';
import IndexContent from '../Index/indexcontent';
import TableExampleCelledStriped from '../Nika/Token/table';
import Airdrop from '../Nika/Token/airdrop';


class ExampleMenu extends Component {
  
  static propTypes = {
  }

  state = { activeItem: 'home'}

  handleItemClick = (e, { name }) => this.setState({ activeItem: name })


  render() {
    const { activeItem } = this.state

    return (
      <Router>

      
      <Menu stackable color="green" inverted  width={3}>
      
      <Menu.Item>
        Nika Labs <img src={science} className="App-logo" alt="logo" />
        </Menu.Item>
         <Menu.Item name='home' as={Link} to='/'/>

       
        
        <Dropdown color="green" inverted item text='Nika'>
      <Dropdown.Menu>
      <Menu.Item name='token' as={Link} to='/Nika/Token/table' />
      <Menu.Item name='airdrop' as={Link} to='/Nika/Token/airdrop' />
      </Dropdown.Menu>
    </Dropdown>
    

        <Menu.Item disabled>Database</Menu.Item>
      </Menu>
      <Route exact path="/" component={IndexContent} />
      <Route path="/Nika/Token/table" component={TableExampleCelledStriped} />
      <Route path="/Nika/Token/airdrop" component={Airdrop} />
</Router>
    )
  }
}

const MenuExampleColoredInvertedMenus = () => {
  const menus = <ExampleMenu />

  return <div>{menus}</div>
}

export default MenuExampleColoredInvertedMenus
