import React, { Component } from 'react'
import { Icon, Accordion, Label, Header, Image, Segment, Message} from 'semantic-ui-react'
import Package from '../../Images/package.png';

export default class Airdrop extends Component {

    state = { activeIndexs: [0 , 1] };
  

    handleClick = (e, titleProps) => {
      const { index } = titleProps;
      const { activeIndexs } = this.state;
      const newIndex = activeIndexs;
  
      const currentIndexPosition = activeIndexs.indexOf(index);
      if (currentIndexPosition > -1) {
        newIndex.splice(currentIndexPosition, 1);
      } else {
        newIndex.push(index);
      }
  
      this.setState({ activeIndexs: newIndex });
    };
    
    
      render() {
    
          const { activeIndexs } = this.state;
  
    
        return (
            <Accordion fluid styled>
            <Accordion.Title active={activeIndexs.includes(0)}
             index={0}
             onClick={this.handleClick}>
           <Header>
       <Label color="green" key="green" >
     <h1>Airdrop</h1>
     </Label>
     <Icon name='dropdown' color="green" size="large"/>
     </Header>
            </Accordion.Title>
            <Accordion.Content active={activeIndexs.includes(0)}>
    
    <Segment textAlign='center'>
    <Image centered size='medium' src={Package} />
    <br />
        <Label basic color='green' size="huge">
         You've been waiting for the real...
        </Label>
        <br />
        <br />
        <Message color="green" size="large">Nika Tokens are distributed fairly to the public. Up to ten million wallets
         can collect Nika. All tokens are free and the airdrop will run until the entire airdrop is empty. There is one
         transaction limit per wallet. All you have to do to get your Nika is send a transaction of 0 ether to the airdrop address.
          If for whatever reason you don't get your Nika, you need to send the transaction with more gas. Latency may happen as
           the ethereum network may be clogged with many transactions, please keep this in mind.
           Please join our <a href="https://t.me/nika_coin">Telegram</a> and read the sticky, you'll find the airdrop address there as well.</Message>
    </Segment>
    </Accordion.Content>
    </Accordion>
        )

}
}