 import React, { Component } from 'react'
 
 
 export default class Pull extends Component {

  constructor(props) {
    super(props);

    this.state = {
      data: null,
    };
  }

  componentDidMount() {
    fetch('http://api.ethplorer.io/getTokenInfo/0xf7F14205898b2b8bC008148E591B1060BE7a50Fb?apiKey=freekey')
      .then(response => response.json())
      .then(data => this.setState({ data }));
  }



  
  render() {
    return (
    <span>
{ String(JSON.stringify(this.state.data).split(":")[10]).split(",")[0] }
    </span>
    );
}
}
