import React, { Component } from 'react'
import { Icon, Table, Accordion, Label, Header, Image, Segment, Message} from 'semantic-ui-react'
import nika from '../../Images/Nika.png'
import Pull from '../../Components/pulldata'


export default class TableExampleCelledStriped extends Component {

    
    state = { activeIndexs: [0, 1] };
  

  handleClick = (e, titleProps) => {
    const { index } = titleProps;
    const { activeIndexs } = this.state;
    const newIndex = activeIndexs;

    const currentIndexPosition = activeIndexs.indexOf(index);
    if (currentIndexPosition > -1) {
      newIndex.splice(currentIndexPosition, 1);
    } else {
      newIndex.push(index);
    }

    this.setState({ activeIndexs: newIndex });
  };
  
  
    render() {
  
        const { activeIndexs } = this.state;

  
      return (

        <Accordion fluid styled>
        <Accordion.Title active={activeIndexs.includes(0)}
         index={0}
         onClick={this.handleClick}>
       <Header>
   <Label color="green" key="green" >
 <h1>Nika's Purposes</h1>
 </Label>
 <Icon name='dropdown' color="green" size="large"/>
 </Header>
        </Accordion.Title>
        <Accordion.Content active={activeIndexs.includes(0)}>

<Segment textAlign='center'>
<Label basic color='green' size='huge'>There are <Pull /> people in <a href="https://etherscan.io/token/0xf7f14205898b2b8bc008148e591b1060be7a50fb">Nika</a> and counting
    </Label>
    <Image circular centered size='medium' src={nika} />
    <Label basic color='green' size="huge">
     Let's power some smart contracts
    </Label>
    <br />
    <br />
    <Message color="green" size="large">Nika Tokens will be used to power transactions for
    countless contracts on the blockchain. Think of it as an app-store, but for contracts. You'll be able
    to do a variety of things on the blockchain, from storing documents, to processing data to perhaps
    even gambling. Our tokens are specifically designed with the end-user in mind and distributed fairly and
    freely to the public for everyone's usage. For the next five years, Nika will act a deflationary token
    to allow for proper tokenomics and to prevent whales from accumulating too much. Please come join us
    on <a href="https://t.me/nika_coin">Telegram</a> and share your thoughts!</Message>
    
    </Segment>
</Accordion.Content>

        <Accordion.Title active={activeIndexs.includes(1)}
         index={1}
         onClick={this.handleClick}>
       <Header>
   <Label color="green" key="green" >
 <h1>Nika Token Details</h1>
 </Label>
 <Icon name='dropdown' color="green" size="large"/>
 </Header>
        </Accordion.Title>
        <Accordion.Content active={activeIndexs.includes(1)}>

  <Table celled striped padded compact definition>

    <Table.Body>
      <Table.Row>
        <Table.Cell collapsing>
          <Icon name='fire' /> Burn Rate
        </Table.Cell>
        <Table.Cell>25% | 20% | 14.285% | 12.5% | 10% | 0%</Table.Cell>
        <Table.Cell>Goes down every year</Table.Cell>
      </Table.Row>

      <Table.Row>
        <Table.Cell collapsing>
          <Icon name='boxes' /> Supply
        </Table.Cell>
        <Table.Cell>50% Airdrop Distribution | 50% Dev Supply</Table.Cell>
        <Table.Cell>10 Billion Total Tokens</Table.Cell>
      </Table.Row>

      <Table.Row>
        <Table.Cell collapsing>
          <Icon name='bitcoin' /> Possible Purposes
        </Table.Cell>
        <Table.Cell>Identity | A.I. | Supply Management | Cryptocurrency 
            | Staking | Voting | Internet of Things | Documentation |
            	Dapps</Table.Cell>
        <Table.Cell>Decentralized Contracts</Table.Cell>
      </Table.Row>

      
      
    </Table.Body>
  </Table>
        </Accordion.Content>
        </Accordion>
)
}
}

